.mainSection{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    min-height: 150px;
    -ms-flex: 1 1 0%;
        flex: 1 1 0%;
}
.mainSection .text-area{
    -ms-flex: 1 1 0%;
        flex: 1 1 0%;
}

.mainSection .text-area > form{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.mainSection .text-area > form > *{
    padding: 15px;
    margin: 10px;
}

.mainSection .todo-list{
    -ms-flex: 2 1 0%;
        flex: 2 1 0%;
    height: 100%;
    border-right: 1px solid #222;
    margin: 20px 0 20px 0;
}

li:first-child{
    margin-top: 40px;
}

.todo{
    text-align: left;
    margin-left: 2em;
    border-top: 1px solid gray;
}
.todo span{
    -ms-flex: 1 1 0%;
        flex: 1 1 0%;
}

.active{
    text-decoration: line-through;
    color: gray;
}

.buttons-into-todo {
    text-align: right;
    float: right;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 35%;
}
.todo-text {
    display: inline-block;
    padding: 5px 0 5px 0;
    margin: 5px 0;
    width: 65%;
}

li {
    clear: both;
}



